import React, { useEffect, useState, useCallback } from 'react';
import { styled } from '@mui/material/styles';
import { Box, CircularProgress, Typography } from '@mui/material';
import { toast } from 'react-toastify';
import ChatInterface from './ChatInterface';

const MainContainer = styled(Box)(({ theme }) => ({
  minHeight: '100vh',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  background: 'linear-gradient(180deg, #1a1a2e 0%, #16162c 100%)',
  flex: 1
}));

const LoadingContainer = styled(Box)(({ theme }) => ({
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  gap: theme.spacing(2),
  padding: theme.spacing(3),
  background: 'rgba(0,0,0,0.5)',
  borderRadius: theme.spacing(1),
  color: 'white'
}));

const VoiceChat = ({ socket }) => {
  const [isInCall, setIsInCall] = useState(false);
  const [micStatus, setMicStatus] = useState({ active: false, stream: null });

  const setupMicrophone = useCallback(async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      setMicStatus({ active: true, stream });
      toast.success('Microphone access granted');
    } catch (err) {
      console.error('Microphone error:', err);
      setMicStatus({ active: false, stream: null });
      toast.error('Microphone access is required. Please allow microphone access and refresh the page.');
    }
  }, []);

  useEffect(() => {
    if (!socket) return;
    
    setupMicrophone();

    return () => {
      if (micStatus.stream) {
        micStatus.stream.getTracks().forEach(track => track.stop());
      }
    };
  }, [socket, setupMicrophone, micStatus.stream]);

  if (!socket) {
    return (
      <MainContainer>
        <LoadingContainer>
          <CircularProgress color="inherit" size={40} />
          <Typography variant="body1">
            Connecting to server...
          </Typography>
        </LoadingContainer>
      </MainContainer>
    );
  }

  return (
    <MainContainer>
      <ChatInterface 
        micStatus={micStatus}
        socket={socket}
        isInCall={isInCall}
        setIsInCall={setIsInCall}
      />
    </MainContainer>
  );
};

export default VoiceChat;
